body {
	
	overflow-x: hidden;
	overflow-y: scroll;
//	overflow-y: hidden;

	background-color: $background-color;
	color: $font-color;

	font-family: "Open-Sans", sans-serif;
	
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}


b, strong { font-weight: bold; }

i, em { font-style: italic; }



