/*************************** Banner Section Styles ****************************/



@mixin banner-top ($media-min, $media-max, $top-min, $top-max) {

	$d: ($top-max - $top-min) / 1%;

	@for $i from 0 through $d {
		$step: ($media-max - $media-min) / ($d + 1);
		$min: $i * $step + $media-min;
		$max: $min + $step;

		@include media('>=' + round($min), '<' + round($max)) {

			$top: $i + $top-min;

//			$top: $top-max - $i;

			top: $top;

//			top: 50%;



//			@include rem(top, $top);

		}
	}
}





@mixin banner-padding-bottom ($media-min, $media-max, $padding-bottom-min, $padding-bottom-max) {

	$d: ($padding-bottom-max - $padding-bottom-min) / 1px;

	@for $i from 0 through $d {
		$step: ($media-max - $media-min) / ($d + 1);
		$min: $i * $step + $media-min;
		$max: $min + $step;

		@include media('>=' + round($min), '<' + round($max)) {

			$padding-bottom: $i + $padding-bottom-min;

			@include rem(padding-bottom, $padding-bottom);

		}
	}
}



//@mixin banner-font-size ($media-min, $media-max, $font-size-min, $font-size-max, $font-size-vw) {
@mixin banner-font-size ($media-min, $media-max, $font-size-min, $font-size-max) {
	$d: ($font-size-max - $font-size-min) / 1px;
	@for $i from 0 through $d {
		$step: ($media-max - $media-min) / ($d + 1);
		$min: $i * $step + $media-min;
		$max: $min + $step;
		@include media('>=' + round($min), '<' + round($max)) {
			$font-size: $i + $font-size-min;
			@include rem(font-size, $font-size);

/*
			@if $font-size-vw != -1 {
				font-size: $font-size-vw;	
			}
*/

//			font-size: $font-size-vw;	


		}
	}
}

@mixin banner-line-height ($media-min, $media-max, $line-height-min, $line-height-max) {

	$d: ($line-height-max - $line-height-min) / 1px;

	@for $i from 0 through $d {
		$step: ($media-max - $media-min) / ($d + 1);
		$min: $i * $step + $media-min;
		$max: $min + $step;

		@include media('>=' + round($min), '<' + round($max)) {

			$line-height: $i + $line-height-min;

			@include rem(line-height, $line-height);

		}
	}
}


@mixin banner-bottom ($media-min, $media-max, $bottom-min, $bottom-max) {

	$d: ($bottom-max - $bottom-min) / 1px;

	@for $i from 0 through $d {
		$step: ($media-max - $media-min) / ($d + 1);
		$min: $i * $step + $media-min;
		$max: $min + $step;

		@include media('>=' + round($min), '<' + round($max)) {

			$bottom: $i + $bottom-min;

			@include rem(bottom, $bottom);

		}
	}
}




